@import url("https://fonts.googleapis.com/css?family=Montserrat");
@import url("https://fonts.googleapis.com/css?family=Work+Sans");
@import url("https://fonts.googleapis.com/css?family=Ubuntu");
body {
  font-family: 'Ubuntu', sans-serif;
  margin: 0; }

* {
  box-sizing: border-box; }

.home {
  margin-top: 60px; }
  .home header {
    padding: 5%;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    background-image: url("/static/img/hero002.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: 75%;
    min-height: 400px; }
    .home header .cta {
      text-align: left;
      padding: 24px 0;
      border-radius: 10px;
      width: 50%; }
      @media (max-width: 1000px) {
        .home header .cta {
          width: 70%;
          background: rgba(255, 255, 255, 0.7);
          padding: 24px 12px; } }
      @media (max-width: 700px) {
        .home header .cta {
          width: 100%;
          text-align: center;
          margin-top: 12%; } }
    .home header h1 {
      margin-top: 0;
      color: #063b84;
      font-size: 28px; }
      @media (max-width: 1000px) {
        .home header h1 {
          font-size: 24px; } }
      @media (max-width: 700px) {
        .home header h1 {
          font-size: 20px; } }
    .home header h2 {
      color: #000; }
    .home header button {
      background-color: #ef8800;
      border: none;
      padding: 16px;
      color: #fff;
      font-weight: bold;
      border-radius: 50px;
      outline: none;
      cursor: pointer;
      text-transform: uppercase; }
  .home .content {
    margin: 60px 5%;
    display: flex; }
    @media (max-width: 700px) {
      .home .content {
        flex-wrap: wrap;
        justify-content: center; } }
    .home .content .icon img {
      height: 200px;
      margin-top: 24px; }
    .home .content article {
      margin-left: 40px; }
      @media (max-width: 700px) {
        .home .content article {
          margin-left: 0px; } }
  .home .cta {
    text-align: center;
    margin-bottom: 30px; }
    .home .cta button {
      background-color: #ef8800;
      border: none;
      padding: 16px;
      color: #fff;
      font-weight: bold;
      border-radius: 50px;
      outline: none;
      cursor: pointer;
      text-transform: uppercase; }



